import React, { Component } from "react";
import { message } from "antd";
//
import Musiclist from "./Musiclist";
import MusicInfo from "./MusicInfo.js";
import MusicTime from "./MusicTime.js";
import MusicControl from "./MusicControl.js";
//
import Music from "../music/music"; //  import

class MusicBox extends Component {
    //
    constructor() {
        super();

        this.state = {            
            currentListIndex: 0, //
            currentTime: 0, //0
            currentTotalTime: 3600, //,
            playStatus: true, //falsetrue
            playVolume: 0.5, // 0 1
            lists: Music, //listspropsMusicstateMusic
            randomPlay: false
        };
    } 
    componentDidMount() {
        //        
        const {playVolume} = this.state;
        let audio = this.refs.audio; //dom
        this.updatePlayStatus();
        this.timeMove();
        audio.addEventListener("loadedmetadata", () => {
            //,/            
            this.setState({
                currentTotalTime: audio.duration
            },() => {                   
                audio.volume = playVolume
            });
        });
    }
    updatePlayStatus = () => {
        //
        const {playStatus} = this.state;
        let audio = this.refs.audio;
        if (playStatus) {
            audio.play();
        } else {
            audio.pause();
        }
    };
    timeMove = () => {
        //
        let audio = this.refs.audio;
        this.timer = setInterval(() => {
            if (this.state.currentTime >= this.state.currentTotalTime) {
                //               
                this.next();
            } else {
                this.setState({
                    currentTime: audio.currentTime
                });
            }
        }, 500);
    };
    play = () => {
        //
        this.setState({
            playStatus: !this.state.playStatus
        },() => {
            this.updatePlayStatus();
        });
    };
    previous = () => {
        //
        const { currentListIndex, lists ,randomPlay} = this.state;
        if(randomPlay){
            this.setState({
                currentListIndex: this.getRandom(lists.length, currentListIndex),
                currentTime: 0
            },() => {
                this.updatePlayStatus();
            });           
        }else{
            if (currentListIndex === 0) {
                message.warning("", 1, () => {
                    this.setState({
                        currentListIndex: lists.length - 1,
                        currentTime: 0
                    },() => {
                        this.updatePlayStatus();
                    });
                });
            } else {
                this.setState({
                    currentListIndex: currentListIndex - 1,
                    currentTime: 0
                },() => {
                    this.updatePlayStatus();
                });
            }
        }
    };
    getRandom = (len, oldIndex) => {
        let newIndex =  Math.floor(Math.random()*len);
        if(newIndex != oldIndex){
            return newIndex
        }else{
            return this.getRandom(len, oldIndex);
        }
    }
    next = () => {
        //
        const { currentListIndex, lists, randomPlay } = this.state;
        if(randomPlay){
            this.setState({
                currentListIndex: this.getRandom(lists.length, currentListIndex),
                currentTime: 0
            },() => {
                this.updatePlayStatus();
            });           
        }else{
            if (currentListIndex + 1 >= lists.length) {
                clearInterval(this.timer);
                message.warning("", 1, () => {
                    this.setState({
                        currentListIndex: 0,
                        currentTime: 0,                   
                    },() => {
                        this.timeMove();
                        this.updatePlayStatus();
                    });
                });
            } else {
                this.setState({
                    currentListIndex: currentListIndex + 1,
                    currentTime: 0
                },() => {
                    this.updatePlayStatus();
                });
            }
        }
    };

    volumeChange = value => {
        // 
        const { playVolume } = this.state;
        let audio = this.refs.audio;
        this.setState({
            playVolume: value / 30
        },() => {
            audio.volume = playVolume;
        });
    };
    proChange = value => {
        //
        const { currentTotalTime } = this.state;
        let audio = this.refs.audio;
        this.setState({
            playStatus: true
        },() => {
            audio.currentTime = value / 2000 * currentTotalTime;
            audio.play();
        });
    };
    timeChange = value => {
        //
        const { currentTotalTime } = this.state;
        let audio = this.refs.audio;
        audio.pause();
        audio.currentTime = value / 2000 * currentTotalTime;
        this.setState({
            currentTime: value / 2000 * currentTotalTime,
            playStatus: false
        });
    };

    getListId = e => {
        //
        const { lists } = this.state;
        let a = e.getAttribute("data-id");
        for (let key in lists) {
            if (lists[key].id == a) {
                this.setState({
                    currentListIndex: +key
                },() => {
                    this.updatePlayStatus();
                });
            }
        }
    };
    updateList = data => {
        const { lists } = this.state;
        if (data) {
            let nextlist = lists.slice(); //lists
            let ex = 0;
            for (let key in nextlist) {
                if (nextlist[key].id === data.id) {
                    ex++;
                }
            }
            if (ex === 0) {
                nextlist.push(data); //
                this.setState({
                    lists: nextlist //
                });
            }
        }
    };
    deleteList = mid => {
        const { lists } = this.state;
        if (lists.length > 1) {
            let nextlist = lists.slice();
            for (let key in nextlist) {
                if (nextlist[key].id == mid) {
                    nextlist.splice(key, 1);
                    this.setState({
                        lists: nextlist //
                    });
                }
            }
        } else {
            message.warning("", 1);
        }
    };
    playTypeChange = () => {
        const {randomPlay} = this.state;
        this.setState({
            randomPlay: !randomPlay
        })
    }
    render() {
        const { lists, currentListIndex, currentTime, currentTotalTime, playStatus} = this.state;
        // let a = this.getRandom(3, 2);
        // console.log(a);
        return (
            <div className="music-box" id="music-box" ref="musicbox">
                <Musiclist
                    lists={lists}
                    getListId={this.getListId}
                    currentListIndex={currentListIndex}
                    hideList={this.hideList}
                    updateList={this.updateList}
                    deleteList={this.deleteList}
                />
                <MusicInfo info={lists[currentListIndex]} />
                <MusicTime
                    currentTime={currentTime}
                    currentTotalTime={currentTotalTime}
                    progress={currentTime / currentTotalTime}
                    proChange={this.proChange}
                    timeChange={this.timeChange}
                />
                <MusicControl
                    isPlay={playStatus}
                    onPlay={this.play}
                    onPrev={this.previous}
                    onNext={this.next}
                    volumeChange={this.volumeChange}
                    playTypeChange={this.playTypeChange}
                />
                <audio
                    id="audio"
                    ref="audio"
                    src={lists[+currentListIndex].audio}
                    type="audio/mp4"
                >
                    
                </audio>
            </div>
        );
    }
}

export default MusicBox;


